body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Open Sans', 'AvantGarde Bk BT', 'Segoe UI', 'Roboto', 'Oxygen', 'Nunito', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', 'Libre Baskerville', 'Gabarito', sans-serif;
